'use client';

import * as React from 'react';
import { notFound, useParams, useRouter } from 'next/navigation';
import { useAuth } from '@/contexts/auth-context';
import type { DebateCase, CaseContention, CaseCard, CaseBlock, SpeechStance } from '@/lib/types';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Card, CardContent, CardHeader, CardTitle, CardFooter, CardDescription } from '@/components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { ArrowLeft, PlusCircle, Trash2, ArrowUp, ArrowDown, Save, BrainCircuit, Wand2, Edit } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import { Separator } from '@/components/ui/separator';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter, DialogClose } from '@/components/ui/dialog';

const DEBATE_CASES_STORAGE_KEY = 'work-session-debate-cases';

export default function CaseEditorPage() {
    const params = useParams();
    const router = useRouter();
    const { user } = useAuth();
    const { toast } = useToast();
    const caseId = params.id as string;
    
    const [caseData, setCaseData] = React.useState<DebateCase | null>(null);
    const [isMounted, setIsMounted] = React.useState(false);

    React.useEffect(() => {
        setIsMounted(true);
        const storedCases = localStorage.getItem(DEBATE_CASES_STORAGE_KEY);
        if (storedCases) {
            try {
                const cases: DebateCase[] = JSON.parse(storedCases);
                const currentCase = cases.find(c => c.id === caseId);
                if (currentCase) {
                    setCaseData(currentCase);
                } else {
                    notFound();
                }
            } catch (e) {
                notFound();
            }
        } else {
             notFound();
        }
    }, [caseId]);

    const handleSave = () => {
        if (!caseData) return;
        const storedCases = localStorage.getItem(DEBATE_CASES_STORAGE_KEY);
        if (storedCases) {
            const cases: DebateCase[] = JSON.parse(storedCases);
            const updatedCases = cases.map(c => c.id === caseId ? caseData : c);
            localStorage.setItem(DEBATE_CASES_STORAGE_KEY, JSON.stringify(updatedCases));
            toast({ title: 'Case Saved!' });
        }
    }

    const updateField = (field: keyof DebateCase, value: any) => {
        setCaseData(prev => prev ? { ...prev, [field]: value } : null);
    }
    
    if (!isMounted || !caseData || !user || caseData.ownerId !== user.id) {
        return null;
    }

    return (
        <div className="flex flex-col gap-6">
            <div className="flex items-center justify-between">
                <Button variant="outline" onClick={() => router.push('/dashboard/debate-events')}>
                    <ArrowLeft className="mr-2" /> Back to Debate Events
                </Button>
                <Button onClick={handleSave}>
                    <Save className="mr-2" /> Save Case
                </Button>
            </div>

            <Card>
                <CardHeader>
                    <CardTitle className="text-2xl font-headline">{caseData.resolution}</CardTitle>
                    <p className="text-muted-foreground">{caseData.type} - <span className="capitalize">{caseData.stance}</span></p>
                </CardHeader>
            </Card>

            <Tabs defaultValue="framework" className="w-full">
                <TabsList className="grid w-full grid-cols-4">
                    <TabsTrigger value="framework">Framework</TabsTrigger>
                    <TabsTrigger value="contentions">Contentions</TabsTrigger>
                    <TabsTrigger value="cards">Cards</TabsTrigger>
                    <TabsTrigger value="blocks">Blocks</TabsTrigger>
                </TabsList>
                
                <TabsContent value="framework" className="mt-4">
                    <FrameworkEditor caseData={caseData} setCaseData={setCaseData} />
                </TabsContent>

                <TabsContent value="contentions" className="mt-4">
                    <ContentionsEditor caseData={caseData} setCaseData={setCaseData} />
                </TabsContent>
                
                <TabsContent value="cards" className="mt-4">
                    <CardsEditor caseData={caseData} setCaseData={setCaseData} />
                </TabsContent>

                <TabsContent value="blocks" className="mt-4">
                    <BlocksEditor caseData={caseData} setCaseData={setCaseData} />
                </TabsContent>
            </Tabs>
        </div>
    );
}

function FrameworkEditor({ caseData, setCaseData }: { caseData: DebateCase, setCaseData: React.Dispatch<React.SetStateAction<DebateCase | null>> }) {
    const [isEditing, setIsEditing] = React.useState(false);
    const [isWizardOpen, setIsWizardOpen] = React.useState(false);

    const handleFrameworkUpdate = (newFramework: string) => {
        setCaseData(prev => prev ? { ...prev, framework: newFramework } : null);
    };

    return (
        <>
            <Card>
                <CardHeader>
                    <div className="flex items-center justify-between">
                        <CardTitle>Framework</CardTitle>
                        <div className="flex gap-2">
                             <Button variant="outline" size="sm" onClick={() => setIsWizardOpen(true)}>
                                <BrainCircuit className="mr-2" /> Wizard
                            </Button>
                            <Button variant="outline" size="sm" onClick={() => setIsEditing(!isEditing)}>
                                <Edit className="mr-2" /> {isEditing ? 'Done' : 'Edit'}
                            </Button>
                        </div>
                    </div>
                </CardHeader>
                <CardContent>
                    <Textarea
                        placeholder="Use the wizard or edit directly to build your framework..."
                        className="h-96 text-base"
                        value={caseData.framework}
                        onChange={e => handleFrameworkUpdate(e.target.value)}
                        readOnly={!isEditing}
                    />
                </CardContent>
            </Card>

            <FrameworkWizardDialog
                isOpen={isWizardOpen}
                onOpenChange={setIsWizardOpen}
                caseData={caseData}
                onFrameworkGenerated={handleFrameworkUpdate}
            />
        </>
    )
}

function ContentionsEditor({ caseData, setCaseData }: { caseData: DebateCase, setCaseData: React.Dispatch<React.SetStateAction<DebateCase | null>> }) {
    const addContention = () => {
        const newContentions = [...(caseData.contentions || []), { id: `contention-${Date.now()}`, title: `Contention ${ (caseData.contentions || []).length + 1}`, text: '' }];
        setCaseData(prev => prev ? { ...prev, contentions: newContentions } : null);
    }

    const updateContention = (id: string, field: 'title' | 'text', value: string) => {
        const updatedContentions = (caseData.contentions || []).map(c => c.id === id ? { ...c, [field]: value } : c);
        setCaseData(prev => prev ? { ...prev, contentions: updatedContentions } : null);
    }

    const removeContention = (id: string) => {
        const newContentions = (caseData.contentions || []).filter(c => c.id !== id);
        setCaseData(prev => prev ? { ...prev, contentions: newContentions } : null);
    }
    
    const moveContention = (index: number, direction: 'up' | 'down') => {
        const newIndex = direction === 'up' ? index - 1 : index + 1;
        const contentions = caseData.contentions || [];
        if (newIndex < 0 || newIndex >= contentions.length) return;
        const newContentions = [...contentions];
        const temp = newContentions[index];
        newContentions[index] = newContentions[newIndex];
        newContentions[newIndex] = temp;
        setCaseData(prev => prev ? { ...prev, contentions: newContentions } : null);
    }
    
    return (
        <Card>
            <CardHeader>
                <div className="flex items-center justify-between">
                    <CardTitle>Contentions</CardTitle>
                    <Button variant="outline" size="sm" onClick={addContention}><PlusCircle className="mr-2"/> Add Contention</Button>
                </div>
            </CardHeader>
            <CardContent className="space-y-4">
                {(caseData.contentions || []).map((contention, index) => (
                    <Card key={contention.id}>
                        <CardHeader className="flex flex-row items-center justify-between p-3">
                            <Input value={contention.title} onChange={e => updateContention(contention.id, 'title', e.target.value)} className="h-8 text-sm font-semibold" />
                            <div className="flex gap-1">
                                <Button size="icon" variant="ghost" className="h-8 w-8" onClick={() => moveContention(index, 'up')} disabled={index === 0}><ArrowUp /></Button>
                                <Button size="icon" variant="ghost" className="h-8 w-8" onClick={() => moveContention(index, 'down')} disabled={index === (caseData.contentions || []).length - 1}><ArrowDown /></Button>
                                <Button size="icon" variant="ghost" className="h-8 w-8 text-destructive" onClick={() => removeContention(contention.id)}><Trash2/></Button>
                            </div>
                        </CardHeader>
                        <CardContent className="p-3 pt-0">
                            <Textarea value={contention.text} onChange={e => updateContention(contention.id, 'text', e.target.value)} placeholder="Contention body..."/>
                        </CardContent>
                    </Card>
                ))}
                 {(caseData.contentions || []).length === 0 && <p className="text-sm text-muted-foreground text-center pt-8">No contentions added yet.</p>}
            </CardContent>
        </Card>
    )
}

function CardsEditor({ caseData, setCaseData }: { caseData: DebateCase, setCaseData: React.Dispatch<React.SetStateAction<DebateCase | null>> }) {
     const addCard = () => {
        const newCards = [...(caseData.cards || []), { id: `card-${Date.now()}`, tag: 'Card Tagline', content: '' }];
        setCaseData(prev => prev ? { ...prev, cards: newCards } : null);
    }

    const updateCard = (id: string, field: 'tag' | 'content', value: string) => {
        const updatedCards = (caseData.cards || []).map(c => c.id === id ? { ...c, [field]: value } : c);
        setCaseData(prev => prev ? { ...prev, cards: updatedCards } : null);
    }

    const removeCard = (id: string) => {
        const newCards = (caseData.cards || []).filter(c => c.id !== id);
        setCaseData(prev => prev ? { ...prev, cards: newCards } : null);
    }

    return (
        <Card>
            <CardHeader>
                <div className="flex items-center justify-between">
                    <CardTitle>Evidence Cards</CardTitle>
                    <Button variant="outline" size="sm" onClick={addCard}><PlusCircle className="mr-2"/> Add Card</Button>
                </div>
            </CardHeader>
            <CardContent className="space-y-4">
                {(caseData.cards || []).map((card) => (
                     <Card key={card.id}>
                        <CardHeader className="flex flex-row items-center justify-between p-3">
                             <Input value={card.tag} onChange={e => updateCard(card.id, 'tag', e.target.value)} className="h-8 text-sm font-semibold" placeholder="Card tagline/summary..."/>
                            <Button size="icon" variant="ghost" className="h-8 w-8 text-destructive" onClick={() => removeCard(card.id)}><Trash2/></Button>
                        </CardHeader>
                        <CardContent className="p-3 pt-0">
                            <Textarea value={card.content} onChange={e => updateCard(card.id, 'content', e.target.value)} placeholder="Enter evidence/quote here..."/>
                        </CardContent>
                    </Card>
                ))}
                 {(caseData.cards || []).length === 0 && <p className="text-sm text-muted-foreground text-center pt-8">No evidence cards added yet.</p>}
            </CardContent>
        </Card>
    );
}

function BlocksEditor({ caseData, setCaseData }: { caseData: DebateCase, setCaseData: React.Dispatch<React.SetStateAction<DebateCase | null>> }) {
     const addBlock = () => {
        const newBlocks = [...(caseData.blocks || []), { id: `block-${Date.now()}`, argument: 'Anticipated Argument', response: '' }];
        setCaseData(prev => prev ? { ...prev, blocks: newBlocks } : null);
    }

    const updateBlock = (id: string, field: 'argument' | 'response', value: string) => {
        const updatedBlocks = (caseData.blocks || []).map(b => b.id === id ? { ...b, [field]: value } : b);
        setCaseData(prev => prev ? { ...prev, blocks: updatedBlocks } : null);
    }

    const removeBlock = (id: string) => {
        const newBlocks = (caseData.blocks || []).filter(b => b.id !== id);
        setCaseData(prev => prev ? { ...prev, blocks: newBlocks } : null);
    }

    return (
        <Card>
            <CardHeader>
                <div className="flex items-center justify-between">
                    <CardTitle>Blocks</CardTitle>
                     <Button variant="outline" size="sm" onClick={addBlock}><PlusCircle className="mr-2"/> Add Block</Button>
                </div>
                 <CardDescription>Prepare responses to arguments you expect to hear from your opponent.</CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
                {(caseData.blocks || []).map((block) => (
                    <Card key={block.id}>
                        <CardHeader className="p-4">
                            <Label>Opponent's Argument</Label>
                            <Input value={block.argument} onChange={e => updateBlock(block.id, 'argument', e.target.value)} placeholder="e.g., 'They will argue that...'" />
                        </CardHeader>
                        <CardContent className="p-4 pt-0">
                            <Label>My Response</Label>
                            <Textarea value={block.response} onChange={e => updateBlock(block.id, 'response', e.target.value)} placeholder="My counter-argument and evidence..." />
                        </CardContent>
                        <CardFooter className="p-4 pt-0 flex justify-end">
                            <Button size="sm" variant="destructive" onClick={() => removeBlock(block.id)}>
                                <Trash2 className="mr-2" /> Remove Block
                            </Button>
                        </CardFooter>
                    </Card>
                ))}
                {(caseData.blocks || []).length === 0 && <p className="text-sm text-muted-foreground text-center pt-8">No blocks prepared yet.</p>}
            </CardContent>
        </Card>
    );
}

function FrameworkWizardDialog({ isOpen, onOpenChange, caseData, onFrameworkGenerated }: { isOpen: boolean, onOpenChange: (open: boolean) => void, caseData: DebateCase, onFrameworkGenerated: (framework: string) => void }) {
    const [agd, setAgd] = React.useState('');
    const [agdAuthor, setAgdAuthor] = React.useState('');
    const [definitions, setDefinitions] = React.useState('');
    const [value, setValue] = React.useState('');
    const [valueCriterion, setValueCriterion] = React.useState('');
    const [observations, setObservations] = React.useState('');
    const [thesis, setThesis] = React.useState('');

    const generateFramework = () => {
        const stanceText = caseData.stance === 'affirmative' ? 'Affirm' : 'Negate';

        const parts = [
            agd,
            (agdAuthor ? `\nBecause I agree with ${agdAuthor}, I must ${stanceText} today's resolution: "${caseData.resolution}".` : `\nI must ${stanceText} today's resolution: "${caseData.resolution}".`),
            definitions ? `\n\nFor clarification of today's debate, I offer the following definitions:\n${definitions}` : '',
            value ? `\n\nFor today's debate we must focus on the value of ${value}.` : '',
            valueCriterion ? `\nMy value criterion for this round is ${valueCriterion}.` : '',
            observations ? `\n\nObservations:\n${observations}` : '',
            thesis ? `\n\n${thesis}` : ''
        ];

        onFrameworkGenerated(parts.filter(Boolean).join(''));
        onOpenChange(false);
    };

    return (
        <Dialog open={isOpen} onOpenChange={onOpenChange}>
            <DialogContent className="sm:max-w-2xl">
                <DialogHeader>
                    <DialogTitle>Framework Wizard</DialogTitle>
                    <DialogDescription>Construct your framework piece by piece.</DialogDescription>
                </DialogHeader>
                <div className="grid grid-cols-2 gap-4 py-4">
                    <div className="space-y-2">
                        <Label>Attention Getting Device (AGD)</Label>
                        <Textarea value={agd} onChange={e => setAgd(e.target.value)} />
                    </div>
                    <div className="space-y-2">
                        <Label>Author of AGD (if applicable)</Label>
                        <Input value={agdAuthor} onChange={e => setAgdAuthor(e.target.value)} />
                    </div>
                    <div className="col-span-2 space-y-2">
                        <Label>Definitions</Label>
                        <Textarea value={definitions} onChange={e => setDefinitions(e.target.value)} placeholder="Term: Definition..." />
                    </div>
                    <div className="space-y-2">
                        <Label>Value</Label>
                        <Input value={value} onChange={e => setValue(e.target.value)} />
                    </div>
                    <div className="space-y-2">
                        <Label>Value Criterion</Label>
                        <Input value={valueCriterion} onChange={e => setValueCriterion(e.target.value)} />
                    </div>
                     <div className="col-span-2 space-y-2">
                        <Label>Observations</Label>
                        <Textarea value={observations} onChange={e => setObservations(e.target.value)} />
                    </div>
                    <div className="col-span-2 space-y-2">
                        <Label>Thesis / Summary</Label>
                        <Textarea value={thesis} onChange={e => setThesis(e.target.value)} />
                    </div>
                </div>
                <DialogFooter>
                    <DialogClose asChild><Button variant="outline">Cancel</Button></DialogClose>
                    <Button onClick={generateFramework}><Wand2 className="mr-2" /> Generate Framework</Button>
                </DialogFooter>
            </DialogContent>
        </Dialog>
    )
}
